import './style/scss/MainApp.scss'
import {React, Component} from 'react'
import Messages from './Components/Messages'
import UserPage from './Components/UserPage'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { faComment } from '@fortawesome/free-solid-svg-icons'
import { faUser } from '@fortawesome/free-solid-svg-icons'
import { faFileAlt } from '@fortawesome/free-solid-svg-icons'
import { faFileInvoice } from '@fortawesome/free-solid-svg-icons'
import { faFileSignature } from '@fortawesome/free-solid-svg-icons'
import { faTable } from '@fortawesome/free-solid-svg-icons'
import { faIdCard } from '@fortawesome/free-solid-svg-icons'
import { faFileContract} from '@fortawesome/free-solid-svg-icons'
import { faFileInvoiceDollar} from '@fortawesome/free-solid-svg-icons'
import { faBook} from '@fortawesome/free-solid-svg-icons'
import { faEnvelopeOpenText} from '@fortawesome/free-solid-svg-icons'
import { faSignOutAlt} from '@fortawesome/free-solid-svg-icons'

const msgIco = <FontAwesomeIcon icon={faComment} />
const userIco = <FontAwesomeIcon icon={faUser} />
const fileIco = <FontAwesomeIcon icon={faFileAlt} />
const fileInvoiceIco = <FontAwesomeIcon icon={faFileInvoice} />
const fileSignatureIco = <FontAwesomeIcon icon={faFileSignature} />
const tableIco = <FontAwesomeIcon icon={faTable} />
const idCardIco = <FontAwesomeIcon icon={faIdCard} />
const fileContractIco = <FontAwesomeIcon icon={faFileContract} />
const fileInvoiceDollarIco = <FontAwesomeIcon icon={faFileInvoiceDollar} />
const bookIco = <FontAwesomeIcon icon={faBook} />
const envelopeOpenTextIco = <FontAwesomeIcon icon={faEnvelopeOpenText} />
const singOutIco = <FontAwesomeIcon icon={faSignOutAlt} />

const navBackgroundColorInActive = "#EFEFEF";
const navBackgroundColorActive = "#E3E3E3";

class MainApp extends Component {
    constructor(props) {
        super(props)

        this.state = {
            active: Messages,
            lastActivePageId: 'messagesPageId1'
        }

        this.handleChangeActivePage = this.handleChangeActivePage.bind(this);
    }

    handleChangeActivePage(event) {
        const activePageId = event.currentTarget.id;
        const lastActivePageId = this.state.lastActivePageId;
        document.getElementById(activePageId).style.backgroundColor = navBackgroundColorActive;
        document.getElementById(lastActivePageId).style.backgroundColor = navBackgroundColorInActive;
        this.state.lastActivePageId = activePageId;

        const activePageIdLast = Number(event.currentTarget.id.slice(-1));
        switch (activePageIdLast) {
            case 1:
                this.setState({active: Messages})
                break;
            case 2:
                this.setState({active: UserPage})
                break;
            default:
                break;
        }
    }


    render() {
        return(
            <div className="mainApp mainApp-outer">
                <div className="mainApp-inner">
                    <nav className="mainApp-inner-nav">
                        <ul className="mainApp-inner-nav-wrapper">
                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="messagesPageId1"
                            >   
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {msgIco}
                                </div>
                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    შეტყობინებები
                                    </div>
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userPageId2"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {userIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    პირადი მონაცემები
                                </div>

                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userSemestersInfoId3"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {fileIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    სასწავლო ბარათი
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userCoursePlanId4"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {fileInvoiceIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    საგ. პროგრამა
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userRegistrationId5"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {fileSignatureIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    აკადემიური რეგისტრაცია  
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userSemesterPlanId6"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {tableIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    სასწავლო ცხრილი
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userMarksPageGeId7"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {idCardIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    ნიშნების ფურცელი
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userMarksPageEngId8"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {idCardIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    ნიშნების ფურცელი(ENG)
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userExercisesId9"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {fileContractIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                 დავალებები
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userFinancesId10"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {fileInvoiceDollarIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    ფინანსები
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userLibraryId11"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {bookIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    ბიბლიოთეკა
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userStatementsId12"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {envelopeOpenTextIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    განცხადებები
                                </div>
                                
                            </li>

                            <li className="mainApp-inner-nav-wrapper-item"
                                onClick={this.handleChangeActivePage}
                                id="userExitId13"
                            >
                                <div className="mainApp-inner-nav-wrapper-item-ico">
                                    {singOutIco}
                                </div>

                                <div className="mainApp-inner-nav-wrapper-item-text">
                                    გასვლა
                                </div>
                                
                            </li>
                        </ul>
                    </nav>

                    <div className="mainApp-inner-activePage">
                        <this.state.active user={this.props.user}/>
                    </div>
                </div>
            </div>
        )
    }
}

export default MainApp;
