import {React, Component} from 'react'
import '../style/scss/ComponentsStyles/UserPage.scss'
import userImg from '../img/userImg.jpg'


class UserPage extends Component {
    constructor(props) {
        super(props) 
        this.user = this.props.user
    }

    render() {
        return(
            <div className="mainApp-inner-activePage-userPage">
                <div className="userPage userPage-outer">
                    <div className="userPage-inner">
                        <aside className="userPage-inner-aside">
                            <figure>
                                <img src={userImg} alt="userImage"/>
                                <figcaption>გიგა ვარდია</figcaption>
                            </figure>
                        </aside>

                        <main className="userPage-inner-main">
                            <div className="userPage-inner-main-wrapper">
                                <div className="userPage-inner-main-wrapper-grid">
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სპეციალობა:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.specialty}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        საფეხური:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.step}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        მიმდინარე სემესტრი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.currentSemester} სემესტრი
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სქესი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.sex}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        ეროვნება:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.nationality}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        დაბადების თარიღი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.birthday}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        საცხოვრებელი ადგილი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.dwellingPlace}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სტატუსი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.status}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სკოლა:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.school}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        სულ კრედიტები:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item"
                                        style={{fontWeight: "bold"}}
                                    >
                                        {this.props.user.userPage.credits}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        GPA:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item"
                                        style={{fontWeight: "bold"}}
                                    >
                                        {this.props.user.userPage.gpa}
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        ტელეფონის ნომერი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item phoneInputWrapper">
                                        <div>
                                            <label>
                                                <input id="phoneInpId" type="text" defaultValue={this.user.userPage.telNumber}/>
                                            </label>
                                        </div>
                                        

                                        <div>
                                            <button id="savePhoneBtn">
                                                შენახვა
                                            </button>
                                        </div>
                                    </div>
                                
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        ჯგუფი:
                                    </div>
                                    
                                    <div className="userPage-inner-main-wrapper-grid-item">
                                        {this.props.user.userPage.group}
                                    </div>
                                </div>
                            </div>
                        </main>
                    </div>
                </div>
            </div>
        )
    }
}

export default UserPage;